// /src/scss/_grid.scss

//       .container
//  row  bootstrap
//@include flex-wrap;
@mixin flex-wrap($gutter: $gutter) {
  display: flex;
  flex-wrap: wrap;
  margin: 0 #{ - $gutter} 0 #{ - $gutter};
}

//       .container
//  row  bootstrap,    flex-wrap: wrap
.row {
  @include flex-wrap;
}

