// @include half;
@mixin half {
  width: calc((100% - (#{$gutter} * 2)) / 2);

  &:not(:nth-of-type(2n)) {
    margin-right: $gutter * 2;
  }
}

// @include third;
@mixin third {
  width: calc((100% - (#{$gutter} * 2 * 2)) / 3);     // 6rem = 1.5 * 2 * 2   //   
  &:not(:nth-of-type(3n)) {
    margin-right: $gutter * 2;
  }
}

// @include fourth;
@mixin fourth {
  width: calc((100% - (#{$gutter} * 2 * 3)) / 4);   // 9rem = 1.5 * 2 * 3   //   
  &:not(:nth-of-type(4n)) {
    margin-right: $gutter * 2;
  }
}


//         
//     .row  @mixin flex-wrap
//@include items-in-row; // . _grid
@mixin items-in-row($qty: 4, $gutter: $gutter) {
  width: calc(100% / #{$qty} - #{$gutter} * 2);
  margin-left: $gutter;
  margin-right: $gutter;
}

//    ,   
// - - 12 
//     .row  @mixin flex-wrap
//@include $span; // . _grid
//@include span(4);  //   4 
//@include span(6);  //   6 
//@include span(12); //    
@mixin span($qty: 4, $columns: 12, $gutter: $gutter) {
  width: calc(#{percentage($qty/$columns)} - #{$gutter * 2});
  margin-left: $gutter;
  margin-right: $gutter;
}